@use 'sass:map';
@use '../../common/scss/abstracts/variables';
@use '../../common/scss/abstracts/functions';
@use '../../common/scss/abstracts/mixins';
@use '../../common/scss/abstracts/placeholders';

button {
    cursor: pointer;
}

.top_visual {
    height: 400px;
    background: #6AE1BC url(../../dig/images/etc/bg_ebookGift_top.png) no-repeat center;
    .contents_inner {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: #fff;

        b {
            font-size: 18px;
            line-height: 28px;
        }
        strong {
            margin: 20px 0;
            font-size: 40px;
            line-height: 56px;
        }
        span {
            font-size: 18px;
            line-height: 28;
        }
    }
}

.topBanner {
    &_type01 {
        position: relative;
        .swiper {
            .swiper-wrapper {
                width: 1200px;
                
                .swiper-slide {
                    display: block;
                    border-radius: 20px 20px 20px 0;
                    overflow: hidden;
    
                    img {
                        width: 100%;
                        height: 340px;
                    }
    
                    &:empty {
                        height: 340px;
                        background-color: #767676;
                    }
                }
            }
            .pagination {
                position: absolute;
                left: 50%;
                top: 265px;
                display: flex;
                align-items: center;
                margin-top: 0;
                font-size: unset;
                transform: translateX(-490px);
                z-index: 9;

                span {
                    font-weight: 600;
                    font-size: 13px;
                    color: #fff;
                }
                .swiper-scrollbar {
                    margin: 0 6px;
                    width: 70px;
                    height: 1px;
                    border-radius: 0;
                    background-color: rgba(255,255,255, .1);
                    .swiper-scrollbar-drag {
                        background-color: #fff;
                    }
                }

                .auto_control {
                    margin-left: 5px;
                    width: 20px;
                    height: 20x;
                    background: url(../../dig/images/common/ico_banner_pause.png) no-repeat center;
                    opacity: .3;
                    text-indent: -1000px;
                    overflow: hidden;

                    &:hover {
                        opacity: 1;
                    }

                    &.pause {
                        background: url(../../dig/images/common/ico_banner_play.png) no-repeat center;
                    }
                }
            }
        }
        .swiper-button-prev, .swiper-button-next {
            top: 170px;
        }
        .swiper-button-prev {
            left: calc(50% - 564px);
        }
        .swiper-button-next {
            right: calc(50% - 564px);
        }

        .text_list {
            position: relative;
            display: flex;
            margin: 0 auto;
            width: 1200px;
            background-color: #fff;
            z-index: 10;
            > div {
                width: 280px;
                position: relative;

                &:hover {
                    button {
                        font-weight: 500;
                        color: #000;

                        &:after {
                            position: absolute;
                            left: 50%;
                            bottom: -1px;
                            width: 100%;
                            height: 2px;
                            background-color: #000;
                            transform: translateX(-50%);
                            animation: sort .10s linear;
                            z-index: 1;
                            content: "";
                        }
                    }
                }

                > button {
                    position: relative;
                    width: 100%;
                    height: 54px;
                    font-size: 15px;
                    color: #767676;
                    text-align: center;
                    border-bottom: 1px solid #eaeaea;
                    background-color: #fff;
                    z-index: 10;

                }

                &:hover > ul {
                    display: flex;
                }

                ul {
                    flex-direction: column-reverse;
                    display: none;
                    position: absolute;
                    bottom: 54px;
                    left: 20px;
                    width: 240px;
                    border-radius: 16px 16px 0 0;
                    overflow: hidden;
                    z-index: 5;

                    li {
                        a {
                            display: block;
                            padding: 0 15px;
                            width: 100%;
                            height: 44px;
                            color: #000;
                            text-align: left;
                            line-height: 43px;
                            box-sizing: border-box;
                            background-color: #fff;

                            &:hover, &.active {
                                color: #fff;
                                background-color: #5668ae;
                            }
                        }
                    }
                }
            }

            > button {
                width: 80px;
                height: 54px;
                text-indent: -1000px;
                overflow: hidden;
                background: url(../../dig/images/common/btn_eventView.png) no-repeat center;
                cursor: pointer;
            }
        }

        .swiper-pagination {
            left: 50%;
            bottom: 0;
        }
    }
    &_type02 {
        position: relative;
        margin-left: calc(50% - 600px);
        .swiper-slide {
            width: 390px;
            height: 340px;
            border-radius: 20px;
            overflow: hidden;
            &:first-child {
                border-bottom-left-radius: 0;
            }
        }

        .swiper-button-next {
            right: 0;
            left: calc(50% + 370px);
        }
    }
    &_type03 {
        position: relative;
        margin-left: calc(50% - 600px);

        .swiper-slide {
            width: fit-content;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 2px 0;

            &:first-child {
                border-top-left-radius: 20px;
                overflow: hidden;
            }
        }

        .swiper-pagination {
            bottom: -36px;
            width: 1200px;
            display: flex;
            justify-content: center;
            gap: 0 20px;

            .swiper-pagination-bullet {
                width: auto;
                height: auto;
                font-weight: 600;
                font-size: 13px;
                background: none;
            }
        }
    }
}

.quickLink {
    position: relative;
    .swiper-container {
        margin: 80px auto 0;
        width: 1200px;
    
        .swiper-wrapper {
            width: 1200px;
    
            .swiper-slide {
                a {
                  display: block;
                  text-align: center;
                  div {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-bottom: 10px;
                    height: 56px;
                  }
