                    font-size: 16px;
                    color: #000;
                    line-height: 100%;
                  }
                }
            }
        }
    }

    .swiper-button-prev {
        left: calc(50% - 664px);
    }
    .swiper-button-next {
        right: calc(50% - 664px);
    }
}

.event_banner {
    margin: 80px auto 0;
    width: 1200px;
    h6 {
        margin-bottom: 20px;
        font-size: 20px;
        color: #000;
    }
}

.todayBook {
    position: relative;
    margin: 80px auto 0;
    width: 1200px;
    h6 {
        margin-bottom: 20px;
        font-size: 20px;
        color: #000;
    }
    .swiper {
        width: 100%;
        overflow: hidden;
    }
    .swiper-button-prev {
        left: calc(50% - 664px);
    }
    .swiper-button-next {
        right: calc(50% - 664px);
    }
    .swiper-button-prev, .swiper-button-next {
        margin-top: -8px;
    }

    .swiper-slide {
        padding-top: 73px;
        .prodDt_detail {
            display: none;
            opacity: 0;
        }

        &-active {
            display: flex;
            gap: 0 20px;
            padding-top: 0;
            .prodDt_cover {
                width: 250px;
                height: 365px;

                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .prodDt_detail {
                display: block;
                width: 296px;
                padding-top: 37px;
                opacity: 1;
                transition: all .3s;

                .prodDt_text {
                    @include mixins.multi_ellipsis(19px, 3, false);
                }
            }
        }
    }
}

.category_type01 {
    position: relative;
    margin-top:80px;

    &.bg {
        padding: 50px 0;
        background-color:#f7f7f7;
            
        .swiper-button-prev, .swiper-button-next {
            top: 260px;
        }
    }

    &.best {
        counter-reset: line-number;
        .prodDt {
            counter-increment: line-number;
            .prodDt_bullet {
                &:before {
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: 8px;
                    width: 24px;
                    height: 24px;
                    font-weight: 600;
                    font-size: 14px;
                    color: #fff;
                    line-height: 24px;
                    border-radius: 6px 0 6px 0;
                    background-color: #767676;
                    content: counter(line-number);
                }
            }

            &:first-child {
                .prodDt_bullet {
                    &:before {
                        background-color: #4dac27;
                    }
                }
            }
        }
    }

    .head {
        display: flex;
        align-items: center;
        margin-bottom: 20px;

        h6 {
            font-size: 20px;
            color: #000;
        }
        .tabMn {
            margin-left: 24px;
            display: flex;
            align-items: center;

            button {
                font-size: 14px;
                color: #767676;

                &.current {
                    font-weight: 600;
                    color: #474c98;
                }

                & + button:before {
                    display: inline-block;
                    margin: 0 13px;
                    width: 1px;
                    height: 10px;
                    background-color: #eaeaea;
                    content: "";
                }
            }
        }
        a {
            margin-left: auto;
            font-size: 15px;

            .ico_plus {
                background-size: 10px 10px;
            }
        }
    }
    .swiper-button-prev {
        left: calc(50% - 664px);
    }
    .swiper-button-next {
        right: calc(50% - 664px);
    }
    .swiper-button-prev, .swiper-button-next {
        top: 210px;
    }
    
    .swiper-slide {
        width: 211px;

        .prodDt_cover {
            width: inherit;
        }

        .prodDt_detail {
            margin-top: 20px;
        }
    }
}

.category_type02 {
    margin-top: 80px;
    .contents_inner {
        display: flex;
        justify-content: space-between;

        .head {
            position: relative;
            width: 310px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            .tabMn {
                display: flex;
                flex-direction: column;
                margin-bottom: 12px;
                width: 100%;
                button {
                    position: relative;
                    padding-left: 20px;
                    min-width: 280px;
                    height: 64px;
                    font-size: 13px;
                    color: #595959;
                    text-align: left;
                    line-height: 63px;
    
                    &.current {
                        font-weight: 600;
                        font-size: 14px;
                        color: #fff;
    
                        &:after {
                            position: absolute;
                            left: 0;
                            top: 0;
                            width: 310px;
                            height: 64px;
                            border-radius: 10px;
                            background-color: #595959;
                            z-index: -1;
                            content: "";
                        }
                    }
    
                    &:after {
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 280px;
                        height: 1px;
                        background-color: #e5e5e5;
                        z-index: -1;
                        content: "";
                    }
    
                    &:last-child:before {
                        position: absolute;
                        left: 0;
                        bottom: 0;
                        width: 280px;
                        height: 1px;
                        background-color: #e5e5e5;
                        z-index: -2;
                        content: "";
                    }
                }
            }
            .btn {
                display: flex;
                align-items: center;
                .swiper-button-prev, .swiper-button-next {
                    position: relative;
                    left: 0;
                    right: 0;
                    top: 0;
                    display: inline-block;
                    margin: 0 6px 0 0;
                    width: 24px;
                    height: 24px;
                    background-image: url(../../common/images/btn_pagination_next.png);
                    background-size: 24px 24px;
                }

                .swiper-pagination {
                    position: relative;
                    left: 0;
                    bottom: 0;
                    padding: 0;
                    transform: none;
                    background-color: unset;

                    .swiper-pagination-current {
                        color: #000;
                    }
                    .swiper-pagination-total {
                        color: #767676;
                    }
                }
            }
            a {
                margin-left: 110px;
                font-size: 12px;
    
                .ico_plus {
                    background-size: 10px 10px;
                }
            }

        }

        .swiper-container {
            width: 810px;
            margin: 0;

            .prodDt_cover {
                margin-bottom: 16px;
            }
        }

    }
}

