.rts {
    margin: 80px auto 0;
    width: 1200px;
    h6 {
        font-size: 20px;
        color: #000;
    }

    &_list {
        display: flex;
        gap: 0 24px;
        margin-top: 20px;
    }

    &_item {
        position: relative;
        padding-top: 30px;
        width: 180px;
        height: 412px;
        text-align: center;
        box-sizing: border-box;
        border: 1px solid #eaeaea;
        border-radius: 16px;

        strong {
            position: relative;
            z-index: 3;
            em {
                color: #6db329;
            }
        }

        .img {
            margin: 20px auto 0;
            width: 118px;
            height: 188px;
            border-radius: 16px 16px 16px 0;
            border: 1px solid #eaeaea;
            box-sizing: border-box;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
            }
        }

        > .text {
            position: relative;
            z-index: 3;
            display: none;
            margin: 20px 20px 0;
            height: 188px;
            font-size: 13px;
            color: #fff;
        }

        > span {
            position: relative;
            z-index: 3;
            margin: 16px 20px 0;
            font-weight: 500;
            font-size: 15px;
            line-height: 23px;
            @include mixins.multi_ellipsis(23px, 2, false);
        }

        > em {
            display: block;
            margin-top: 5px;
            font-size: 13px;
            color: #595959;
            text-align: center;
        }

        .detail {
            position: relative;
            z-index: 3;
            display: none;
            align-items: center;
            justify-content: center;
            margin-top: 8px;
            width: 78px;
            height: 32px;
            font-weight: 500;
            font-size: 12px;
            color: #fff;
            border: 1px solid #383838;
            border-radius: 4px;
            background-color: #484848;

            &:before {
                display: inline-block;
                margin-right: 4px;
                width: 13px;
                height: 11px;
                background: url(../../dig/images/common/ico_book.png) no-repeat center;
                content: "";
            }
        }

        .btn_more_plus_text {
            position: absolute;
            z-index: 3;
            left: 50%;
            bottom: 14px;
            transform: translateX(-50%);
            .text {
                font-size: 12px;
                line-height: 23px;
            }
            .ico_plus {
                background-size: 10px 10px;
            }
        }

        &:hover {
            .img, > em {
                display: none;
            }
            > .text {
                text-align: justify;
                @include mixins.multi_ellipsis(19px, 10, false);
            }
            > span {
                color: #fff;
            }
            .detail {                
                display: inline-flex;
            }
                
            .btn_more_plus_text {
                .text {
                    color: #fff;
                }
                .ico_plus {
                    background-image: url(../../common/images/ico_plus_h10_bold_white@2x.png);
                }
            }

            &:before {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                border-radius: 16px;
                opacity: .6;
                background-color: #000;
                z-index: 1;
                content: "";
            }
            &:after {
                position: absolute;
                left: 0;
                top: 0;
                display: block;
                width: 100%;
                height: 100%;
                border-radius: 16px;
                backdrop-filter: blur(50px);
                z-index: 2;
                content: "";
            }
        }
    }
}

